.continer{
flex-wrap: wrap;
justify-content: flex-end;
}
.content{
display: flex;
}
.mainContent{
    padding: 0;
    list-style: none;
    flex: 4
}
.aside{
    height: 50%;
    flex-shrink: 0;
    padding: 20px 20px 24px;
    -webkit-tap-highlight-color:transparent;
    box-sizing: border-box;
    flex:1 
}
.title{
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 26px;
    line-height: 1.2;
}
.collection{
    margin: 10px 0;
}
.price{
    margin: 10px 0;
}
.btn-add-remove{
    font-family: inherit;
    font-size: 16px;
    line-height: 28px;
    cursor: pointer;
    display: flex;
    width: 100%;
    justify-content: space-between;
    color: inherit;
    padding: 0;
    border: 0;
    outline: 0;
    background: none;
}

.card-body{
    flex: 1 14 auto;
    min-height: 1px;
    padding: 1.25rem;
}
.card{
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0,0,0,.125);
    border-radius: .25rem;
    margin: 1em;
}

.card-img, .card-img-top {
    border-top-left-radius: calc(.25rem - 1px);
    border-top-right-radius: calc(.25rem - 1px);
}

.card-img, .card-img-bottom, .card-img-top {
    flex-shrink: 0;
    width: 300px;
}

img {
    vertical-align: middle;
    border-style: none;
    height:400px;
}

*, ::after, ::before {
    box-sizing: border-box;
}
.card-title {
    margin-bottom: .75rem;
}
.dot {
height: 20px;
width: 20px;
border-radius: 50%;
display: inline-block;
border: 1px solid #c9c9c9;
margin: 10px 4px;
position: relative;
cursor: pointer;

}

